<template>
<v-container grid-list-xl fluid>
    <v-layout row wrap>
        <file_upload :id="user.data.id"></file_upload>
        <v-flex sm12>
            <h3>Funcionarios</h3>
        </v-flex>
        <v-flex xs12 sm12 md12>
            <v-card>
                <v-card-title>
                    Funcionários
                    <v-btn v-show="user.data.role === 'gerente'" @click="cadastro" fab dark small color="green">
                        <v-icon>add</v-icon>
                    </v-btn>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="search" append-icon="search" label="Buscar" single-line hide-details></v-text-field>
                </v-card-title>
                <v-data-table :headers="headers" :items="filteredList" :search="search">
                    <template slot="items" slot-scope="props">
                        <td>{{ props.item.name }}</td>
                        <td>
                            <v-icon @click="getPerfil(props.item._id)">input</v-icon>
                        </td>
                    </template>
                    <v-alert slot="no-results" :value="true" color="error" icon="warning">
                        Your search for "{{ search }}" found no results.
                    </v-alert>
                </v-data-table>
            </v-card>
        </v-flex>
    </v-layout>
</v-container>
</template>

<script>
import services from "../services/userService";
import file_upload from '../components/client';
export default {
    data() {
        return {
            user: {},
            search: '',
            users: [],
            headers: [{
                    text: 'Nome',
                    align: 'left',
                    sortable: false,
                    value: 'name'
                },
            ]
        }
    },
    components: {
        file_upload
    },

    methods: {
        getPerfil(id) {
            this.$router.push("/user/" + id);
        },
        cadastro() {
            this.$router.push("/cadastro/" + this.user.data.id);
        },
        listarUsuarios() {
            services.listarFuncionarios(this.user.data.id).then(r => {
                if (r.status == 200) {
                    this.users = r.data;
                }
            });
        },
        getUser(data) {
            this.$router.push("/user/" + data);
        }
    },
    mounted() {
        this.user = JSON.parse(this.$cookie.get("user"));
        if (this.$route.params.id != null) {
            this.user.data.id = this.$route.params.id;
        }
        this.listarUsuarios();
    },
    computed: {
        filteredList() {
            return this.users.filter(post => {
                return post.name.toLowerCase().includes(this.search.toLowerCase())
            })
        }
    }

}
</script>
